@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

body {
  font-family: "Rubik", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: rgb(255, 255, 255);
  max-width: 1100px;
  margin: 0 auto;
}

a {
  color: lightgray;
}

.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.lost-card{background-color:#a34d5d}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name{font-size:4rem;margin-bottom:20px;position:absolute;left:320px;top:170px;text-align:center;width:650px}.TeamPage .win-loss-section{justify-self:center;padding:40px;height:250px;text-align:center;position:absolute;right:50px;top:20px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{font-size:1.5rem;-webkit-align-self:center;align-self:center;justify-self:center}.TeamPageLogo{width:200px;height:180px;position:absolute;left:100px;top:100px}

.H2H-Header{text-align:center;margin:20px}.H2H-team-grid{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:30px;grid-row-gap:40px}.H2H-selected-teams{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.dashed{width:100px;border:1px dashed #c4c4c4}.H2HSelectedTeamTile{background:white;border:1px solid red;height:125px;border-radius:50%;-moz-border-radius:50%;-webkit-border-radius:50%;width:125px;margin-top:10px;margin-bottom:40px}.H2HselectedTeamLogos{height:100px;position:relative}img{max-height:100%;max-width:100%;width:auto;height:auto;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto;padding-top:25px}.v{font-size:large;color:red}.table-container{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;grid-gap:10px;gap:10px;padding:1rem}table{border-collapse:collapse;width:100%}th,td{border:1px solid #ffffff;text-align:left;padding:8px;font-size:15px}th{background-color:#19ceeb}td{background-color:#282c34}.stacked-bar-graph{width:100%;height:25px;color:#414042}.stacked-bar-graph span{display:inline-block;height:100%;box-sizing:border-box;font-weight:bold;font-family:arial, sans-serif;color:white;padding:5px}.stacked-bar-graph .bar-1{background:#4da375}.stacked-bar-graph .bar-3{background:#a34d5d}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}

.MatchPage{display:grid;grid-template-columns:1fr 5fr;grid-column-gap:20px}.MatchPage .year-selector{justify-self:end;padding:20px}.MatchPage .page-heading{width:600px;margin:10px 10px}.MatchPage .No-Matches-Played{color:#f88379;width:600px;margin:10px 10px}

.HomePage .IPLlogo{width:100px;height:150px;position:absolute;left:100px;top:0px}.HomePage .app-name{font-size:3rem;margin-left:125px;margin-top:40px;margin-bottom:20px}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-column-gap:50px;grid-row-gap:20px}*{box-sizing:border-box}.btn-hover{width:150px;font-size:16px;font-weight:600;color:#fff;cursor:pointer;margin:20px;height:50px;text-align:center;border:none;background-size:300% 100%;border-radius:50px;transition:all 0.4s ease-in-out}.btn-hover:hover{background-position:100% 0;transition:all 0.4s ease-in-out}.btn-hover:focus{outline:none}.btn-hover.color-1{background-image:linear-gradient(to right, #25aae1, #40e495, #30dd8a, #2bb673);box-shadow:0 4px 15px 0 rgba(49,196,190,0.75)}.fancy-button-head-to-head{text-align:center;position:absolute;right:100px;top:20px;padding:10px}.fancy-button-dashboard{text-align:center;position:absolute;left:80px;top:20px;padding:10px}.fancy-button-dashboard-match{text-align:center;position:absolute;right:80px;top:0px}

.TeamTile{height:300px;position:relative;border:1px solid white;padding:10px}.TeamName{position:absolute;font-size:1.2em;font-weight:bold;left:50%;bottom:0px;text-align:center;-webkit-transform:translateX(-50%);transform:translateX(-50%);padding:10px}.teamLogos{height:200px;width:200px;position:relative}img{max-height:100%;max-width:100%;width:auto;height:auto;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto}

.H2HTeamTile{height:190px;position:relative;padding:10px}.H2Hcheckbox{display:inline-block;width:200px;height:190px;padding-top:10px}#checkboxes input[type="checkbox"]{display:none}#checkboxes input[type="checkbox"]:checked+.H2Hcheckbox{border:2px solid red;border-radius:25px}.TeamName{position:absolute;font-size:1em;left:45%;bottom:0px;text-align:center;-webkit-transform:translateX(-50%);transform:translateX(-50%)}.H2HteamLogos{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;height:100px;position:relative}img{max-height:100%;max-width:100%;width:auto;height:auto;position:absolute;top:0;bottom:0;left:0;right:0;margin:auto}

